.text-columns{

  &-items{
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
  }

  &-item{
    width: calc(50% - 20px);
  }

  &_3{
    .text-columns-item{
      width: calc(33.33% - 26.6px);
    }
  }

  &_center{
    .text-columns-items{
      align-items: center;
    }
  }
}